import { NavLink, Routes, Route, Outlet } from "react-router-dom";
import { Suspense } from "react";

export const Layout = () => {
  return (
    <div>
      <nav>
        <ul>
          <li>
            <NavLink to="/"></NavLink>
          </li>
          <li>
            <NavLink to="/dogs"></NavLink>
          </li>
        </ul>
      </nav>
      <main>
        <Suspense fallback={<div>...</div>}>
          <Outlet />
        </Suspense>
      </main>
    </div>
  );
};
